import Img from "gatsby-image"

import React, { Component } from "react"

import * as educationStyles from "../../styles/Components/educationTile.module.css"

export default class EducationTile extends Component {
  render() {
    return (
      <div className={`row ${educationStyles.maindiv}`}>
        <div className={educationStyles.imgDiv}>
          {/* <img  src="" /> */}
          <div className={educationStyles.midImgDiv}>
            <Img
              className={educationStyles.img}
              fluid={this.props.fluid}
              alt={this.props.institute}
              title={this.props.institute}
            />
          </div>
        </div>
        <div className={educationStyles.sideOuterDiv}>
          <div className={educationStyles.sideMidDiv}>
            <div className={educationStyles.sideDiv}>
              <div className={educationStyles.degree}>{this.props.degree}</div>
              <div className={educationStyles.institute}>
                {this.props.institute}
              </div>
              {this.props.location ? (
                <div className={educationStyles.location}>
                  {this.props.location}
                </div>
              ) : null}
              <div className={educationStyles.year}>{this.props.year}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
